import React from 'react';
import './MovieDetail.scss';

const MovieDetail = ({selectedItem}) => {
    if(!selectedItem) {
        return <div></div>
    }

    return (
        <div className="modal_wrap modal-window" id="modal-p01">
            <div className="modal-inner">
                <div className="detail_image">
                    <div>
                    <img src={`https://image.tmdb.org/t/p/w500/${selectedItem.backdrop_path}`} alt={selectedItem.title} />
                    </div>
                </div>
                <div className="detail_description">
                    <p className="detail_description_title">{selectedItem.title}</p>
                    <p className="detail_description_date">{selectedItem.release_date}</p>
                    <p className="detail_description_txt">{selectedItem.overview}</p>
                </div>               
            </div>
            <a href="#!" className="modal-close">&times;</a>
        </div>
    );
};

export default MovieDetail;