import React from 'react';
import './MovieItem.scss';
import sample from './sample.jpg'

const MovieItem = ({ movie, onMovieSelectFromList }) => {

    return (        
        <li key = {movie.id} className="movie_item" onClick={()=> onMovieSelectFromList(movie)}>
            <div className="movie_image">
                <div>
                    <img src={`https://image.tmdb.org/t/p/w500/${movie.backdrop_path}`} 
                    onError={(e)=>{e.target.onerror = null; e.target.src=`${sample}`}}/>              
                </div>
            </div>
            <div className="movie_description">
                <p className="movie_description_title">{movie.title}</p>
                <p className="movie_description_date">{movie.release_date}</p>
            </div>
        </li>
    );
}

export default MovieItem;